<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/company' }">公司管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改资源' : '选择资源' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="资源列表" prop="resourceList">
          <el-tree ref="tree" :data="rightList" show-checkbox node-key="id" @check="checkChange" :default-checked-keys="checkList" :props="defaultProps" default-expand-all check-on-click-node> </el-tree>
        </el-form-item>
        <el-form-item>
          <el-button v-if="$route.params.type === 'edit'" type="primary" @click="editEnterprise">确定</el-button>
          <el-button v-else type="primary" @click="addEnterprise">确定</el-button>
          <el-button v-if="$route.params.type === 'edit'" type="primary" @click="returnEditEnterprise">返回</el-button>
          <el-button v-else type="primary" @click="returnAddEnterprise">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// import menuList from '@/utils/menu'
export default {
  data() {
    return {
      ruleForm: {
        id: '',
        name: '',
        code: '',
        creditCode: '',
        phone: '',
        address: '',
        fax: '',
        legalPerson: '',
        legalCertType: '',
        legalCertCode: '',
        legalGender: '',
        legalNation: '',
        resourceList: []
      },
      rules: {
        name: [{ required: true, message: '请输入公司名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入内部企业编号', trigger: 'blur' }],
        creditCode: [{ required: true, message: '请输入统一社会信用代码', trigger: 'blur' }]
      },
      defaultProps: {
        children: 'childNode',
        label: 'name'
      },
      rightList: [],
      checkList: [], // 得到的数据
      checkListStr: '' //  已改变的数据
    }
  },

  methods: {
    // 权限选择变化回调监听
    checkChange(currNode, checkedObj) {
      // console.log(checkedObj.checkedKeys)
      this.checkList = checkedObj.checkedKeys
      this.checkListStr = this.$roleJoin(checkedObj.checkedKeys)
    },
    //增加时选择权限确定
    addEnterprise() {
      console.log(this.checkList)
      this.ruleForm.resourceList = this.checkList
      this.$router.push({ path: '/editEnterprise', name: 'EditEnterprise', params: { type: 'add', info: this.ruleForm } })
    },
    //增加时选择权限返回
    returnAddEnterprise() {
      this.$router.push({ path: '/editEnterprise', name: 'EditEnterprise', params: { type: 'add', info: this.ruleForm } })
    },
    //修改时选择权限确定
    editEnterprise() {
      console.log(this.checkList)
      this.ruleForm.resourceList = this.checkList
      this.httpPost('/basic/enterprise/mod/' + this.ruleForm.id, this.ruleForm).then(res => {
        this.$message.success('设置成功'),
          this.$router.push({
            path: '/company',
            query: {}
          })
      })
    },
    //修改时选择权限返回
    returnEditEnterprise() {
      this.$router.push({ path: '/company', name: 'Company', params: {} })
    }
  },

  mounted() {
    let params = {
      state: 1, //1:使用中，0:禁用中
      size: 999,
      page: 1,
      total: ''
    }
    this.httpPost('/basic/resource/query', params).then(res => {
      var menuList = []
      for (var i = 0; i < res.rows.length; i++) {
        if (res.rows[i].parentId == 0) {
          res.rows[i].childNode = []
          menuList.push(res.rows[i])
        } else {
          for (var j = 0; j < menuList.length; j++) {
            if (menuList[j].id == res.rows[i].parentId) {
              menuList[j].childNode.push(res.rows[i])
              break
            }
          }
        }
      }
      console.log(menuList)
      const menu = Object.assign([], menuList)
      this.menuArr = Object.assign([], menuList)
      this.rightList = menu
    })
    this.ruleForm = this.$route.params.info
    this.checkList = this.$route.params.info.resourceList
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else {
      this.type = 'edit'
      this.httpPost('/basic/enterprise/query/resource/' + this.$route.params.info.id, '').then(res => {
        this.checkList = res
        this.ruleForm.resourceList = res
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
  .el-tree {
    width: auto !important;
    display: flex;
    padding-top: 8px;
    /deep/.el-tree-node {
      margin-right: 10px;
    }
  }
}
</style>
