<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/resource' }">资源管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改资源' : '添加资源' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="资源名称" prop="name">
          <el-input size="small" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="资源编码" prop="code">
          <el-input size="small" v-model="ruleForm.code"></el-input>
        </el-form-item>
        <!--<el-form-item label="资源类型" prop="type">-->
          <!--<el-radio-group v-model="ruleForm.type">-->
            <!--<el-radio label="M">Menu菜单</el-radio>-->
            <!--<el-radio label="B">Button按钮</el-radio>-->
            <!--<el-radio label="S">Script脚本</el-radio>-->
          <!--</el-radio-group>-->
        <!--</el-form-item>-->
        <el-form-item label="默认资源" prop="defaultAssign">
          <el-radio-group v-model="ruleForm.defaultAssign">
            <el-radio label="0">否</el-radio>
            <el-radio label="1">是</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="资源地址" prop="href">
          <el-input size="small" v-model="ruleForm.href"></el-input>
        </el-form-item>
        <el-form-item label="父级资源" prop="parentName">
          <el-autocomplete class="inline-input" v-model="ruleForm.parentName" @blur="mustSel" :fetch-suggestions="querySearch" placeholder="请选择父级资源" @select="handleSelect" clearable></el-autocomplete>
        </el-form-item>
        <el-form-item label="备注" prop="memo">
          <el-input size="small" v-model="ruleForm.memo"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        id: '',
        name: '',
        code: '',
        parentId: '',
        parentName: '',
        memo: '',
        href: '',
        type: 'M',
        defaultAssign:'0'
      },
      restaurants: [],
      rules: {
        name: [{ required: true, message: '请输入资源名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入资源编码', trigger: 'blur' }],
        parentId: [{ required: true, message: '请输入父级资源', trigger: 'change' }]
      }
    }
  },

  methods: {
    mustSel() {
      if (!this.ruleForm.parentId) this.ruleForm.parentName = null
    },
    handleSelect(item) {
      console.log(item)
      // this.groupSelect = item
      // this.deviceCategoryId = item.id
      this.ruleForm.parentId = item.id
      this.ruleForm.parentName = item.name
    },
    //输入联想
    querySearch(queryString, cb) {
      var queryForm = {
        size: 10,
        page: 1,
        total: '',
        name: queryString,
        parentId:0,
        state: 1
      }
      this.httpPost('/basic/resource/query', queryForm).then(res => {
        res.rows.unshift({ id: '0', name: '顶级资源' })
        res.rows.map(i => {
          i.value = i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },

    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm)
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          console.log(params)
          if (this.type == 'add') {
            this.httpPost('/basic/resource/add', params)
              .then(res => {
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/resource',
                  query: {}
                })
              })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          } else {
            this.httpPost('/basic/resource/mod/' + params.id, params)
              .then(res => {
                this.$message.success('设置成功'),
                  this.$router.push({
                    path: '/resource',
                    query: {}
                  })
              })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else {
      this.ruleForm = this.$route.params.info
      this.type = 'edit'
      this.ruleForm.defaultAssign =  this.ruleForm.defaultAssign.toString()
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
}
</style>
